/* LoginForm */

.login{
  width:340px;
  margin:0px auto;
  font-size:12px;
  color:#333;
}
.login h2{
  text-align:center;
  font-size:20px;
  padding:10px;color:#5f0080;
  background:none;
}
.login input{
  width:340px;
  height:44px;
  border:1px solid #ccc;
  font-size:14px;
}
.id_pw_search{text-align:right;}
input[type=submit]{
  border:none;
  background:#5f0080;
  color:#fff;
  font-weight:bold;
  margin-top:20px
}
input[type=button]{
  border:1px solid #5f0080;
  color:#5f0080;
  background:#fff;
  margin:5px 0px;
  font-weight:bold;
}